.el-button {
  font-family: SiYuan;
  font-size: .18rem;
}
.el-button--primary {
  background-color: #5AB0F3;
  border-color: #5AB0F3;
}
.el-button--primary:focus,
.el-button--primary:hover {
  background-color: #5AB0F3;
  border-color: #5AB0F3;
}
.el-button--warning {
  background-color: #F3Af1B;
  border-color: #F3Af1B;
  color: #000000;
}
.el-button--warning:focus,
.el-button--warning:hover {
  background-color: #F3Af1B;
  border-color: #F3Af1B;
  color: #000000;
}
.el-button--none {
  background-color: transparent;
  border-color: transparent;
  color: #ffffff;
}
.el-button--none:focus,
.el-button--none:hover {
  background-color: #F3Af1B;
  border-color: #F3Af1B;
  color: #000000;
}
.whiteColor,
.el-dialog__title,
.el-dialog__headerbtn .el-dialog__close,
.el-form .el-form-item__label {
  color: #ffffff;
}
.el-dialog {
  background-color: #1B418D;
  border-radius: .04rem;
}
.el-dialog__header {
  padding: .4rem .6rem .1rem;
}
.el-dialog__body {
  padding: .3rem .6rem;
  max-height: 6.34rem;
  overflow-y: auto;
  overflow: auto;
}
.el-dialog__body .row {
  margin: 0;
  line-height: 1.5;
}
.el-dialog__body::-webkit-scrollbar {
  width: .08rem;
  background: transparent;
}
.el-dialog__body::-webkit-scrollbar-corner,
.el-dialog__body::-webkit-scrollbar-thumb,
.el-dialog__body::-webkit-scrollbar-track {
  border-radius: .04rem;
}
.el-dialog__body::-webkit-scrollbar-corner,
.el-dialog__body::-webkit-scrollbar-track {
  /* 滚动条轨道 */
  background-color: rgba(180, 160, 120, 0);
  box-shadow: inset 0 0 0.01rem rgba(180, 160, 120, 0.5);
}
.el-dialog__body::-webkit-scrollbar-thumb {
  /* 滚动条手柄 */
  background-color: rgba(255, 255, 255, 0.3);
}
.el-dialog__title {
  font-weight: 500;
  font-size: .22rem;
  line-height: normal;
}
.el-dialog__title .iconfont {
  font-size: .24rem;
  margin-left: .16rem;
  color: #F3Af1B;
}
.el-dialog__headerbtn {
  top: .24rem;
  right: .24rem;
}
.el-dialog__footer {
  padding: .1rem .2rem .4rem;
}
.el-dialog__footer .dialog-footer {
  text-align: center;
}
.el-dialog__footer .dialog-footer .el-button {
  padding: .12rem .42rem;
  font-size: .18rem;
}
.el-dialog__footer .dialog-footer .el-button + .el-button {
  margin-left: .35rem;
}
.el-dialog__footer .dialog-footer .el-button--warning {
  color: #ffffff;
}
.el-dialog__footer .dialog-footer .el-button--warning:focus,
.el-dialog__footer .dialog-footer .el-button--warning:hover {
  color: #ffffff;
}
.el-input__inner {
  height: .4rem;
  background-color: #2551A2;
  border-color: #2551A2;
  font-size: .18rem;
  color: #ffffff;
}
.el-input__inner:hover {
  border-color: #5AB0F3;
}
.el-select:hover .el-input__inner {
  border-color: #5AB0F3;
}
.el-form .el-form-item {
  display: flex;
}
.el-form .el-form-item__label {
  width: 1.04rem !important;
  min-width: 98px;
  font-family: SiYuan;
  font-size: .18rem;
  line-height: .4rem;
  text-align: justify;
}
.el-form .el-form-item__content {
  width: 100%;
  margin-left: 0 !important;
  line-height: .4rem;
}
.el-form .el-form-item__content .el-select .el-input.is-focus .el-input__inner {
  border-color: #5AB0F3;
}
.el-textarea .el-textarea__inner {
  min-height: 1.48rem !important;
  border-radius: .04rem;
  background-color: #2551A2;
  border-color: #2551A2;
  font-size: .14rem;
  color: #ffffff;
  resize: none;
}
.el-textarea .el-input__count {
  right: .1rem;
  bottom: .1rem;
  background-color: #2551A2;
  font-size: .14rem;
  line-height: .22rem;
  color: #8DA0C6;
}
input,
textarea {
  font-size: .18rem;
  font-family: SiYuan;
}
input::placeholder,
textarea::placeholder {
  color: #8DA0C6 !important;
}
.el-input__prefix {
  left: .2rem;
}
.el-input--prefix .el-input__inner {
  padding-left: .54rem;
}
.el-form-item__error {
  color: #F2476B;
}
.el-form-item {
  margin-bottom: .2rem;
}
.el-input__suffix {
  color: #52D4E0;
  cursor: pointer;
}
.el-tag {
  height: .4rem;
  margin-right: .18rem;
  margin-bottom: .1rem;
  padding: 0 .1rem;
  font-size: .18rem;
  line-height: .4rem;
  border-radius: .04rem;
}
.el-tag.el-tag--primary {
  background-color: #5AB0F3;
  border-color: #5AB0F3;
  color: #ffffff;
}
.el-tag.el-tag--primary .el-icon-close {
  background-color: #F2476B;
  line-height: .16rem;
  color: #ffffff;
}
.el-tag.el-tag--primary .el-icon-close:hover {
  background-color: #F2476B;
}
.el-tag .el-icon-close {
  width: .18rem;
  height: .18rem;
  right: -0.05rem;
  font-size: .12rem;
}
.el-radio-group .el-radio-button__inner {
  padding: .13rem .22rem;
  border-color: #5AB0F3;
  background-color: transparent;
  font-size: .18rem;
  color: #ffffff;
}
.el-radio-group .el-radio-button__orig-radio:checked + .el-radio-button__inner {
  background-color: #5AB0F3;
  border-color: #5AB0F3;
}
.el-radio-group .el-radio-button:first-child .el-radio-button__inner {
  border-color: #5AB0F3;
}
.el-radio {
  color: #ffffff;
  cursor: pointer;
  margin-right: .4rem;
}
.el-radio .el-radio__inner {
  width: .2rem;
  height: .2rem;
  border-width: .02rem;
  border-color: #ffffff;
  background: transparent;
}
.el-radio .el-radio__label {
  padding-left: .06rem;
}
.el-radio .el-radio__label,
.el-radio .el-radio__input.is-checked + .el-radio__label {
  font-size: .18rem;
  color: #ffffff;
}
.el-radio.is-checked .el-radio__inner {
  border-color: #ffffff;
  background: transparent;
}
.el-radio.is-checked .el-radio__inner::after {
  width: .1rem;
  height: .1rem;
  background-color: #F3Af1B;
}
.el-tooltip__popper.is-dark {
  background-color: #F3Af1B;
  font-size: .18rem;
  padding: .1rem;
  color: #000000;
}
.el-tooltip__popper .popper__arrow,
.el-tooltip__popper .popper__arrow::after {
  border-color: #F3Af1B transparent transparent transparent !important;
}
.el-tree {
  background-color: transparent;
}
.el-tree .el-tree-node .el-tree-node__label {
  font-size: .18rem;
  color: #ffffff;
}
.el-tree .el-tree-node .el-tree-node__content:hover {
  background-color: transparent;
}
.el-tree .el-tree-node .el-tree-node__content:hover .el-tree-node__label {
  color: #F3Af1B;
}
.el-tree .el-tree-node:focus > .el-tree-node__content {
  background-color: transparent;
}
.el-tree .el-tree-node:focus > .el-tree-node__content .el-tree-node__label {
  color: #F3Af1B;
}
.el-icon-caret-right:before {
  font-family: "iconfont" !important;
  content: "\e621";
  color: #ffffff;
}
.el-tree-node__expand-icon.expanded {
  transform: none;
}
.el-tree-node__expand-icon.expanded::before {
  font-family: "iconfont" !important;
  content: "\e623";
  color: #ffffff;
}
.el-tree-node__expand-icon.is-leaf::before {
  color: transparent;
}
.el-checkbox__input.is-checked .el-checkbox__inner,
.el-checkbox__input.is-indeterminate .el-checkbox__inner {
  background-color: #F3Af1B;
  border-color: #F3Af1B;
}
.el-checkbox__inner {
  border-color: #ffffff;
}
.el-checkbox__inner:hover {
  border-color: #F3Af1B;
}
.el-tree-node.is-focusable.is-checked .el-tree-node__label {
  color: #F3Af1B;
}
.el-table {
  background-color: transparent;
  font-size: .18rem;
  border-color: #2551A2;
  color: #ffffff;
}
.el-table th,
.el-table tr {
  background-color: transparent;
  color: #ffffff;
}
.el-table th {
  padding: .04rem 0;
  text-align: center;
}
.el-table th.is-leaf {
  border-color: #2551A2;
  font-weight: normal;
}
.el-table td {
  padding: .01rem 0;
  border-color: #2551A2 !important;
  text-align: center;
}
.el-table--border td {
  border-color: #2551A2;
}
.el-table__fixed-right::before,
.el-table__fixed::before,
.el-table::before {
  background-color: #2551A2;
}
.el-table__body tr.hover-row > td {
  background-color: transparent !important;
}
.el-button--text {
  font-weight: normal;
  font-size: .18rem;
  color: #52D4E0;
}
.el-date-picker__time-header {
  border-color: #5AB0F3;
  font-size: .12rem;
  padding: .08rem .05rem .05rem;
}
.el-picker-panel__body-wrapper {
  background-color: #2551A2;
}
.el-picker-panel__sidebar {
  width: 1.1rem;
  min-width: 110px;
  background-color: #2551A2;
  border-color: #5AB0F3;
}
.el-picker-panel__footer {
  background-color: #2551A2;
  border-color: #5AB0F3;
  padding: .04rem;
}
.el-picker-panel__footer .el-button.is-plain {
  background-color: #F3Af1B;
  border-color: #F3Af1B;
  color: #000000;
}
.el-picker-panel__shortcut {
  font-size: .18rem;
  line-height: .28rem;
  color: #ffffff;
}
.el-date-table {
  font-size: .14rem;
}
.el-date-table th,
.el-date-table__row {
  padding: .05rem;
  color: #ffffff;
}
.el-date-table th {
  border-color: #5AB0F3;
}
.el-date-table td div {
  height: .3rem;
}
.prev-month {
  color: #8DA0C6;
}
.el-popper[x-placement^=top] .popper__arrow {
  border-top-color: #2551A2;
  border-bottom-color: #5AB0F3;
}
.el-popper[x-placement^=top] .popper__arrow::after {
  border-top-color: #2551A2;
}
.el-popper[x-placement^=bottom] .popper__arrow {
  border-bottom-color: #1B418D;
}
.el-popper[x-placement^=bottom] .popper__arrow::after {
  border-bottom-color: #2551A2;
}
.el-picker-panel {
  border-color: #1B418D;
  line-height: .3rem;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.52);
}
.el-picker-panel .el-input__inner {
  height: .32rem;
  line-height: .32rem;
  background-color: #1B418D;
}
.el-time-panel {
  width: 1.8rem;
  min-width: 180px;
  background-color: #2551A2;
  border-color: #2551A2;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.52);
}
.el-time-panel__footer {
  border-color: #5AB0F3;
  padding: .04rem;
  height: .36rem;
  line-height: .25rem;
}
.el-time-panel__footer .el-time-panel__btn {
  font-size: .14rem;
}
.el-time-panel__footer .el-time-panel__btn.cancel {
  color: #ffffff;
}
.el-date-table td span {
  width: .24rem;
  height: .24rem;
  line-height: .24rem;
}
.el-time-panel__content::before,
.el-time-panel__content::after {
  margin-top: -0.08rem;
  height: .32rem;
  padding-top: .06rem;
  border-color: #5AB0F3;
}
.el-time-spinner__item:hover:not(.disabled):not(.active) {
  background-color: #1B418D;
}
.el-time-spinner__item {
  color: #8DA0C6;
  height: .32rem;
  line-height: .32rem;
  font-size: .12rem;
}
.el-time-spinner__item.active:not(.disabled) {
  color: #5AB0F3;
}
.el-picker-panel__icon-btn,
.el-date-picker__header-label {
  font-size: .18rem;
  color: #5AB0F3;
}
.el-date-picker.has-sidebar.has-time {
  width: 4.34rem;
  min-width: 434px;
}
.el-date-picker .el-picker-panel__content {
  width: 2.92rem;
}
.el-popper .popper__arrow {
  border-width: .06rem;
}
.el-popper .popper__arrow::after {
  border-width: .06rem;
}
.el-popper[x-placement^=bottom] .popper__arrow::after {
  top: .01rem;
  margin-left: -0.06rem;
}
.el-picker-panel__sidebar + .el-picker-panel__body {
  margin-left: 1.1rem;
}
@media screen and (max-height: 1080px) {
  .el-picker-panel__sidebar + .el-picker-panel__body {
    margin-left: 110px;
  }
}
.el-select-dropdown {
  border: none;
  box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.52);
}
.el-select-dropdown__list {
  background-color: #2551A2;
  border-color: #2551A2;
}
.el-select-dropdown__item {
  font-size: .18rem;
  color: #ffffff;
}
.el-select-dropdown__item.hover,
.el-select-dropdown__item:hover {
  background-color: #1B418D;
}
.el-select .el-input .el-select__caret,
.el-input__icon {
  width: .25rem;
  font-size: .2rem;
  line-height: .4rem;
  color: #5AB0F3;
}
